/* Table */

table {
    /* border: 1px solid black; */
    /* font: 16px "Open Sans", Helvetica, Arial, sans-serif; */
    /* border: 1px solid gray; */
    border-radius: 5%;
    border-spacing: 0 100px;
    border-collapse: seperated;
    /* box-shadow: 10px 5px rgb(187, 187, 187); */
}
thead {
    min-height: 300px;
}
tr {
    background-color: rgb(228, 240, 245);
}


th, td {
    border-top: 4px solid rgb(255, 255, 255);
    border-bottom: 4px solid rgb(255, 255, 255);
    padding:4px 6px;
}

td {
    padding: 0.2em 0.7em;
}

th {
      border: 4px solid rgb(255, 255, 255);
}
thead > tr:last-of-type > th:nth-of-type(1) {
    background-color: rgb(255, 253, 225);
    color: rgb(179, 134, 0);
    border-radius: 20%;
}

thead > tr:last-of-type > th:nth-of-type(2) {
    min-width: 110px;
    background-color: rgb(245, 210, 253);
    color: rgb(139, 0, 139);
    border-radius: 20%;
}
  
thead > tr:last-of-type > th:nth-of-type(3) {
    min-width: 83px;
    background-color: rgb(252, 200, 200);
    color: rgb(255, 0, 0);
    border-radius: 20%;
}
  
thead > tr:last-of-type > th:nth-of-type(4) {
    min-width: 111px;
    background-color: rgb(197, 250, 197);
    color: rgb(0, 102, 0);
    border-radius: 20%;
}
  
thead > tr:last-of-type > th:nth-of-type(5) {
    min-width: 85px;
    background-color: rgb(198, 202, 197);
    color: rgb(64, 74, 66);
    border-radius: 20%;
}

tbody > tr:nth-of-type(even) {
